.Calendar {
    border-collapse: collapse;
    padding: 0;
    box-sizing: border-box;
}

.Calendar th,
.Calendar td {
    padding: 0.5rem;
    text-align: center;
}

.Day .Day-inner:hover {
    background: #eee;
    color: #000;
}

.Day.today .Day-inner{
    background: #666;
    color: #fff;
}

.Day.selected .Day-inner{
    background: #2980b9;
    color: #fff;
}

