/* MY STYLES */
.Calendar-wrapper {
    position: fixed;
    top:0px; left:0px;
    border: 2px solid black;
    border-radius: 10px;
    opacity: 0;
    background-color: white;
    -webkit-transform: translateX(-92%) translateY(1rem);
            transform: translateX(-92%) translateY(1rem);
    -webkit-transition: opacity 0.5s cubic-bezier(0.785, 0.135, 0.15, 0.86), -webkit-transform 0.5s cubic-bezier(0.785, 0.135, 0.15, 0.86);
    transition: opacity 0.5s cubic-bezier(0.785, 0.135, 0.15, 0.86), -webkit-transform 0.5s cubic-bezier(0.785, 0.135, 0.15, 0.86);
    transition: opacity 0.5s cubic-bezier(0.785, 0.135, 0.15, 0.86), transform 0.5s cubic-bezier(0.785, 0.135, 0.15, 0.86);
    transition: opacity 0.5s cubic-bezier(0.785, 0.135, 0.15, 0.86), transform 0.5s cubic-bezier(0.785, 0.135, 0.15, 0.86), -webkit-transform 0.5s cubic-bezier(0.785, 0.135, 0.15, 0.86);
    z-index: 5;
}


.Day-inner {
    height: 25px;
    width: 25px;
    border-radius: 25px;
    background-color: white;
    border: 1px solid #ccc;
    padding: 2px;
    cursor: pointer;
}

.Day-inner:focus, .nav-inner:focus {
    outline: none;
}

.Day.other-month .Day-inner{
    background: #ccc;
    cursor: not-allowed;
}

.nav-inner {
    height: 25px;
    width: 25px;
    border-radius: 25px;
    background-color: #2c3e50;
    color: white;
    border: 2px solid  #2c3e50;
    cursor: pointer;
}



